<template>
  <PageContainer title="系統公告">
    <div class="grid grid-cols-1 lg:grid-cols-2 gap-5 lg:gap-8">
      <Card
        v-for="item in fakeAnnouncementData"
        :key="item.id"
        :title="item.title"
        :date="item.date"
        @onClickMore="handleClickMore(item.id)"
      ></Card>
    </div>
    <Pagination></Pagination>
  </PageContainer>
</template>
<script>
const fakeAnnouncementData = [];
for (let i = 0; i <= 10; i++) {
  const title =
    i % 3 === 0
      ? "testtesttesttesttesttesttesttesttesttesttesttesttesttesttest"
      : "test";
  fakeAnnouncementData.push({
    id: i,
    title,
    date: "2020/02/02",
  });
}
import PageContainer from "@/components/PageContainer";
import Pagination from "@/components/Pagination";
import Card from "./components/Card";
export default {
  components: {
    PageContainer,
    Pagination,
    Card,
  },
  data() {
    return {
      fakeAnnouncementData,
    };
  },
  methods: {
    handleClickMore(id) {
      this.$router.push(`/member/announcement/detail/${id}`);
    },
  },
};
</script>
